import { component, html, useState } from "haunted";

/**
 *
 * @param percentage
 * @returns {*}
 */
const validatePercentage = percentage => {
  if (isNaN(percentage) || Math.sign(percentage) === -1 || !percentage) return 0;
  if (percentage > 100) return 100;
  return Number(percentage);
}

/**
 * @return {TemplateResult}
 */
function MyApp({holas, numero}) {
  const [hola, setHola] = useState('');
  const [nuevoNumero, setNuevoNumero] = useState(validatePercentage(numero));
  return html`
    <h2>User Page</h2>
    <h3>${holas}${hola}</h3>
    <p>${numero}</p>
    <p>${nuevoNumero}</p>
    <button type="button" @click=${() => setHola(hola + 'Felipe')}>say hello</button>
    <button type="button" @click=${() => setNuevoNumero(nuevoNumero + 10)}>say hello</button>
  `;
}

MyApp.observedAttributes = ['holas','numero'];

customElements.define("my-app", component(MyApp));
